.superInput {
    border: 3px solid #7072ff;
    border-radius: 5px;
}

.errorInput {
    border: 3px solid #ff0000;
    border-radius: 5px;
    background: #ff5454;
    opacity: 0.8;
}

.error {
    color: red;
}
.inp {
    background: #61dafb;
}
.inp:focus {
    outline: none;
}